html, body {
    margin: 0;
    height: 100%;
}
.FullContainer {
    display: grid;
    grid-template-rows: 2em auto 2em;
    position: absolute;
    margin: 0px;
    top: 0px;
    bottom: 0px;
    width: 100%;
    height: 90%;
}
.AppContainer {
    display: grid;
    grid-template-rows: 2em auto 2em;
    position: absolute;
    margin: 0px;
    top: 5em;
    bottom: 0px;
    width: 100%;
    height: 100%-3em;
}
.NavBarContainer {
  grid-template-columns: auto 30em;
}
.windowrow{
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  width: 100% !important;
  height: 1900% !important;
}
.driverstationrow{
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  width: 100% !important;
  height: 1900% !important;
}
.toprow{
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  justify-content: space-around;
  width: 100% !important;
  height: 3em !important;
}
.col {
  background-color: #efefef;
  border-radius: .5em;
  flex: 1  1 auto;
  margin: .1em .1em;
  display: flex;
}
.nogreycol{
  border-radius: .5em;
  flex: 1  1 auto;
  margin: .1em .1em;
  display: flex;
}
.col-full{
  width: 90%;
}
.square-col-half{
  width: 25% !important;
  height: 25% !important;
}
